import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { MatSelectModule } from '@angular/material/select';
import { CharityService, Goods } from '../../charity.service';

@Component({
  selector: 'app-check-out',
  templateUrl: './check-out.component.html',
  styleUrls: ['./check-out.component.css']
})
export class CheckOutComponent implements OnInit {
	selectedId = 0;
	goods: Goods[] = [];
	
  constructor(private charityService: CharityService) {}
	
	ngOnInit() {
		this.getCheckoutGoods();
	}
	
	changeCheckout(selectedId: number) {
		this.charityService.changeCheckout(selectedId)
			.subscribe(
			  response => {
					console.log(response);
				},
				error => {
					console.error(error);
				}
			);	
	}
	
	getCheckoutGoods() {
		this.charityService.getCheckoutGoods()
			.subscribe(
				resp => {
					if(resp.body['code'] === '200'){
						let res = resp.body['results'];
						for(let i = 0; i < res.length; i++){
						  let g: Goods = {
								id: res[i]['id'],
								kind: res[i]['kind'],
								quantity: res[i]['quantity'],
								amount: res[i]['amount'],
								targetLocation: res[i]['target-location'],
								donor: res[i]['donor'],
								storeLocation: res[i]['store-location'],
								checkOut: res[i]['check-out']
							};
							this.goods.push(g);
						}
					}
				},
				(err)=>{
					this.charityService.handleError(err);
				},
				()=>console.log('The Post observable is now completed.')
			)
	}
}
